<template>
	<div class="selects">
		<Select
			:title="currentSelectValue.title"
			:options="currentSelectValue.options"
			:operation="operation"
			@select="handleSelect"
		>
		</Select>
	</div>
</template>
<script>
import Bus from '../utils/Bus'
import Select from './common/Select'

export default {
	name: 'LevelSelects',
	data(){
		return {
			selects:[
				{
					name: 'diffcult',
					value: {
						title: 'ÈÄâÊã©ÈöæÂ∫¶',
						options: [
							{
								text: 'ÁÆÄÂçï',
								value: 'easy'
							},
							{
								text: '‰∏≠Á≠â',
								value: 'medium'
							},
							{
								text: 'ËæÉÈöæ',
								value: 'hard'
							},
						]
					}
				},
				{
					name: 'level',
					value: {
						title: 'ÈÄâÊã©ÂÖ≥Âç°',
						options: [
							{
								text: '1',
								value: 0
							},
							{
								text: '2',
								value: 1
							},
							{
								text: '3',
								value: 2
							},
						]
					},
				}
			],
			data: {},
			currentSelect: 0,
			operation: {
				value: 'none'
			}
		}
	},
	computed: {
		currentSelectValue(){ // ÂΩìÂÖàÈÄâÊã©ÁöÑÂÄº
			return this.selects[this.currentSelect].value
		}
	},
	mounted(){
		this.registerKey()
	},
	methods: {
		/**
		 * Ê≥®ÂÜåÊéßÂà∂‰∫ã‰ª∂
		 */
		registerKey(){
			Bus.$on('control', (operation) => {
				this.operation = {
					value: operation
				}
			})
		},
		/**
		 * Â§ÑÁêÜÈÄâÊã©ÁöÑÂÄº
		 */
		handleSelect(value){
			let currentSelectName = this.selects[this.currentSelect].name
			let currentSelectValue = this.selects[this.currentSelect].value.options[value].value
			this.$set(this.data, currentSelectName, currentSelectValue)
			if (this.currentSelect !== this.selects.length - 1) {
				this.currentSelect++
			} else {
				this.$emit('selects', this.data)
				this.currentSelect = 0
			}	
		}
	},
	components: {
		Select
	}
}
</script>
<style>
.selects{
	position: absolute;
	width: 70%;
	height: 35%;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -60%);
}
</style>

