<template>
	<div class="game-board">
		<!-- ÈùôÊÄÅItem -->
		<table class="board-table">
			<tr
				v-for="(raw, rawIndex) in staticItems"
				:key="rawIndex"
			>
				<Item 
					v-for="(col, colIndex) in raw"
					class="cell"
					:key="rawIndex+'-'+colIndex"
					:type="col"
					:position="rawIndex+'-'+colIndex"
				>
				</Item>
			</tr>
		</table>
		<!-- Âä®ÊÄÅItem -->
		<Item
			v-for="item in boxsAndPerson"
			:key="item.id"
			:type="item.type"
			class="cell dynamic"
			:style="getCSSFromPosi(item.position)"
		>
		</Item>
	</div>
</template>
<script>
import OPERATION from '../constant/Operation'
import ITEMS from '../constant/Item.js'
import Matrix from '../utils/Matrix.js'
import Bus from '../utils/Bus'
import Item from './Item'

// TODO: ÈîôËØØÊéßÂà∂
export default {
	name: 'GameBoard',
	props: {
		mapData: {
			type: Array
		},
		isGaming: {
			type: Boolean
		}
	},
	data(){
		return {
			staticItems: [[]], // ‰∏çÂèØÁßªÂä®ÁöÑItem, Â¢ôÂ£Å|ÊîæÁΩÆÁÇπ
			dynamicItems: [[]], // ÂèØÁßªÂä®ÁöÑItem, ‰∫∫Áâ©|ÁÆ±Â≠ê
			history: [], // Ê≠•È™§ËÆ∞ÂΩï
			lastPositions: [] // Ê≠§Ê¨°Êìç‰ΩúÁßªÂä®ÁöÑËÆ∞ÂΩï
		}
	},
	mounted(){
		this.registerControl()
		this.initData()
		this.checkStatus()
	},
	watch: {
		mapData(){ // Âú∞ÂõæÊï∞ÊçÆÊîπÂèòÊó∂ÂºÄÂßãÊñ∞Ê∏∏Êàè
			this.initData()
			this.checkStatus()
			this.history = []
		}
	},
	computed: {
		person(){ // ‰∫∫Áâ©Êï∞ÊçÆ
			let personPosi
			Matrix.forEach(this.dynamicItems, (item, position) => {
				if (item.type === 5) {
					personPosi = position
				}
			})
			if (personPosi) {
				return {
					position: personPosi,
					type: 5
				}
			} else {
				return {}
			}
		},
		boxs(){ // ÁÆ±Â≠êÊï∞ÊçÆ
			let all = []
			Matrix.forEach(this.dynamicItems, (item, position) => {
				if (item.type === 2) {
					let {type, id, canMove} = item
					all.push({
						position,
						type,
						id,
						canMove
					})
				}
			})
			return all
		},
		boxsAndPerson(){
			if (Object.keys(this.person).length) {
				return this.boxs.concat(this.person)
			}
			return this.boxs
		},
		places(){ // ÊîæÁΩÆÁÇπ
			let all = []
			Matrix.forEach(this.staticItems, (item, position) => {
				if (item === 4) {
					all.push(position)
				}
			})
			return all
		},
		isSuccess(){ // Âà§Êñ≠ÊòØÂê¶Âú®ÊàêÂäü, Âç≥ÁÆ±Â≠êÈÉΩÂú®ÊîæÁΩÆÁÇπ
			let boxPosi = this.boxs.map(box => box.position.toString())
			return this.places.every(place => {
				place = place.toString()
				return boxPosi.includes(place)
			})
		},
		isFailed(){ // Âà§Êñ≠ÊòØÂê¶Â§±Ë¥•, Âç≥ÁÆ±Â≠êÂú®ÈÉΩËßíËêΩ‰∏î‰∏çÈÉΩÂú®ÊîæÁΩÆÁÇπ
			return this.boxs.every(box => !box.canMove)
		}
	},
	methods: {
		/**
		 * Âà§Êñ≠Ê∏∏ÊàèÂ§±Ë¥•|ÊàêÂäü|ÁªßÁª≠
		 */
		willGameContinue(){
			this.checkStatus()
			if (this.isSuccess) {
				this.$emit('result', true)
				return
			}
			if (this.isFailed) {
				this.$emit('result', false)
				return
			}
		},
		/**
		 * Â∞ùËØïÁßªÂä®‰∏Ä‰∏™Áâ©ÂìÅ
		 */
		go(position, direction){
			let itemPosi = position
			let willGoPosi = this.calcPosi(itemPosi, direction) // Áâ©ÂìÅÂ∞ÜË¶ÅÁßªÂä®ÁöÑ‰ΩçÁΩÆ
			let willGoItem = this.dynamicItems[willGoPosi[0]][willGoPosi[1]] // Áâ©ÂìÅ‰ø°ÊÅØ
			if (this.canMove(willGoPosi)) { // ÁßªÂä®Âà∞Á©∫Ê∞îÊàñÊîæÁΩÆÁÇπ
				this.moveItem(itemPosi, willGoPosi)
				this.checkBoxs()
				return true
			} else if (willGoItem.type === 2) { // ÁßªÂä®ÁöÑÂà∞ÁöÑ‰ΩçÁΩÆ‰∏∫ÁÆ±Â≠ê
				if (!willGoItem.canMove) {
					return false
				}
				let res = this.go(willGoPosi, direction)
				if (res) {
					this.moveItem(itemPosi, willGoPosi)
					this.checkBoxs()
					return true
				}
			}
			return false
		},
		/**
		 * Â∞ùËØïÁßªÂä®‰∫∫Áâ©
		 */
		goPerson(direction){
			let hasGo = this.go(this.person.position, direction)
			if (hasGo) {
				this.record()
				this.willGameContinue()
			}
		},
		/**
		 * ÁßªÂä®Áâ©ÂìÅ
		 */
		moveItem(from, to, record = true){ // ÁßªÂä®Item, ÈªòËÆ§ItemÂú®fromÂ§Ñ
			if (record) {
				this.lastPositions.push({
					from,
					to
				})
			}
			let [x, y] = from
			let [n, m] = to
			let item = this.dynamicItems[x][y]
			this.$set(this.dynamicItems[n], m, item)
			this.$set(this.dynamicItems[x], y, 0)
		},
		/**
		 * Ê†πÊçÆÊñπÂêëËÆ°ÁÆóÁßªÂä®Âà∞ÁöÑ‰ΩçÁΩÆ
		 */
		calcPosi(position, direction){
			if (direction === 'up') {
				return [position[0] - 1, position[1]]
			}
			if (direction === 'down') {
				return [position[0] + 1, position[1]]
			}
			if (direction === 'left') {
				return [position[0], position[1] - 1]
			}
			if (direction === 'right') {
				return [position[0], position[1] + 1]
			}
		},
		/**
		 * Âà§Êñ≠ÊòØÂê¶ÂèØ‰ª•ÁßªÂä®Âà∞positionÂ§Ñ
		 */
		canMove(position){
			let [x, y] = position
			let item = this.dynamicItems[x][y] // ÂÖàÊ£ÄÊü•Âä®ÊÄÅÁâ©ÂìÅ
			let itemType = item.type || item
			if (itemType === 2) { // ËØ•Â§ÑÊúâÁÆ±Â≠êÊó∂
				return false
			}
			itemType = this.staticItems[x][y] // ÂÜçÊ£ÄÊü•ÈùôÊÄÅÁâ©ÂìÅ
			if (ITEMS[itemType].canThrough) {
				return true
			}
			return false
		},
		/**
		 * ËÆ∞ÂΩïÊú¨Ê¨°Êìç‰ΩúÈÄ†ÊàêÁöÑÁßªÂä®
		 */
		record(){
			this.history.push(this.lastPositions)
			this.lastPositions = []
		},
		/**
		 * Êí§ÈîÄ‰∏äÊ¨°Êìç‰Ωú
		 */
		undo(){
			if (this.history.length) { // ÂΩìÂéÜÂè≤ËÆ∞ÂΩï‰∏≠ÊúâÊìç‰ΩúÊó∂
				let last = this.history.pop().reverse()
				last.forEach(posi => {
					this.moveItem(posi.to, posi.from, false)
				})
				this.checkBoxs() // ÈáçÁÆóÁÆ±Â≠êÁöÑÁä∂ÊÄÅ
				this.checkStatus()
			}
		},
		/**
		 * Ê£ÄÊü•Ê∏∏ÊàèÁä∂ÊÄÅÂπ∂emit
		 */
		checkStatus(){
			let boxsPosi = this.boxs.map(box => box.position)
			let count = 0
			this.places.forEach(place => {
				boxsPosi.forEach(box => {
					if (place.toString() === box.toString()) {
						count++
					}
				})
			})
			this.$nextTick(() => {
				this.$emit('status', {
					steps: this.history.length,
					done: [count, boxsPosi.length]
				})
			})
		},
		/**
		 * Ê£ÄÊµãÁÆ±Â≠êÊòØÂê¶ËøòËÉΩÁßªÂä®
		 */
		checkBoxs(){
			let hasBlock = [] // ËØ•‰ΩçÁΩÆ‰∏ä‰∏ãÂ∑¶Âè≥ÊòØÂê¶ÈòªÂ°û
			this.boxs.forEach(box => {
				let around = Matrix.getAround(this.staticItems, box.position) // Ê£ÄÊü•Âë®Âõ¥ÁöÑÈùôÊÄÅÁâ©ÂìÅ
				around.forEach(item => {
					if (!ITEMS[item.value].canThrough) {
						hasBlock.push(1)
					} else {
						hasBlock.push(0)
					}
				})
				around = Matrix.getAround(this.dynamicItems, box.position) // Ê£ÄÊü•Âë®Âõ¥‰∏çÂèØÁßªÂä®ÁÆ±Â≠ê
				around.forEach((item, index) => {
					item = item.value
					if (item && !item.canMove) {
						hasBlock[index] = 1
					}
				})
				let count = hasBlock.reduce((acc, cur) => acc + cur) // ÈòªÂ°ûÈù¢Êï∞
				if (count >= 3) { // ‰∏â\ÂõõÈù¢ÈòªÂ°ûÂàôÂÜªÁªì
					this.freezeItem(box.position)
				} else if (count === 2) { // ‰∏§Èù¢ÈòªÂ°û
					let noBlock = hasBlock[0] === 1 && hasBlock[2] === 1 // ‰∏ä‰∏ã‰∏çÈòªÂ°û
					noBlock = noBlock || (hasBlock[1] === 1 && hasBlock[3] === 1) // Â∑¶Âè≥‰∏çÈòªÂ°û
					if (!noBlock) { // ËøûÁª≠‰∏§Èù¢, ÂàôÂøÖÈòªÂ°û
						this.freezeItem(box.position)
					} else {
						this.unFreezeItem(box.position)
					}
				} else {
					this.unFreezeItem(box.position)
				}
				
				hasBlock = []
			})
			
		},
		/**
		 * ÂÜªÁªìÁÆ±Â≠ê
		 */
		freezeItem(position){
			let [x, y] = position
			this.$set(this.dynamicItems[x], y , {
				...this.dynamicItems[x][y],
				canMove: false
			})
		},
		/**
		 * Ëß£ÂÜªÁÆ±Â≠ê(ÂõûÈÄÄÁä∂ÊÄÅÊó∂‰ΩøÁî®)
		 */
		unFreezeItem(position){
			let [x, y] = position
			this.$set(this.dynamicItems[x], y , {
				...this.dynamicItems[x][y],
				canMove: true
			})
		},
		/**
		 * ÈáçÊñ∞ÂºÄÂßã
		 */
		refresh(){
			this.initData()
			this.history = []
			this.checkStatus()
		},
		/**
		 * Ê†πÊçÆ‰ΩçÁΩÆËÆ°ÁÆóCSSÂÄº
		 */
		getCSSFromPosi(position){
			if (position) {
				return {
					top: `${position[0] * 40 + 10}px`,
					left: `${position[1] * 40 + 10}px`
				}
			}
		},
		/**
		 * Ê≥®ÂÜåÊéßÂà∂‰∫ã‰ª∂
		 */
		registerControl(){
			let directionMap = ['UP', 'DOWN', 'LEFT', 'RIGHT']
			let that = this
			let map = {
				[OPERATION.return](){
					that.undo()
				},
				[OPERATION.reload](){
					that.refresh()
				},
				[OPERATION.cancel](){
				}
			}
			Bus.$on('control', operation => {
				if (!this.isGaming) {
					return
				}
				if (directionMap.includes(operation)) {
					that.goPerson(operation.toLocaleLowerCase())
				} else {
					map[operation]()
				}
			})
		},
		/**
		 * ÂàÜÁ¶ªÂéüÂßãÊï∞ÊçÆ, ÂàÜ‰∏∫ÈùôÊÄÅItem(Â¢ô|ÊîæÁΩÆÁÇπ), Âä®ÊÄÅItem(‰∫∫Áâ©|ÁÆ±Â≠ê)
		 */
		initData(){
			this.staticItems = Matrix.map(this.mapData, value => {
				if (!ITEMS[value].canMove) {
					return value
				}
				return 0
			})
			let id = 0
			this.dynamicItems = Matrix.map(this.mapData, value => {
				if (ITEMS[value].canMove) {
					return {
						type: value,
						id: id++,
						canMove: true
					}
				}
				return 0
			})
		}
	},
	components: {
		Item
	}
}
</script>
<style>
.game-board{
	position: relative;
	border-radius: 20px;
	padding: 10px;
}
.board-table{
	border-spacing: 0;
	border-collapse:collapse;
}
.cell{
	width: 40px;
	height: 40px;
	font-size: 1.5em;
	padding: 0;
	transition: all 0.2s ease;
}
.dynamic{
	position: absolute;
	margin-top: 4px;
}
</style>
