export default {
	/**
	 * ÈÅçÂéÜÁü©Èòµ, ÈÄöËøáÂõûË∞ÉÊù•Ëé∑ÂæóÂÖÉÁ¥†ÁöÑÂÄº‰∏éÂùêÊ†á
	 *
	 * @param {[[]]} matrix
	 * @param {Function} cb
	 */
	forEach(matrix, cb){
		matrix.forEach((raw, rawIndex) => {
			raw.forEach((value, colIndex) => {
				cb(value, [rawIndex, colIndex])
			})
		})
	},
	/**
	 * ÈÅçÂéÜÁü©Èòµ, ÈÄöËøáÂõûË∞ÉÊù•ËøáÊª§ÂÄº‰∏éÂùêÊ†á
	 *
	 * @param {[[]]} matrix
	 * @param {Function} cb
	 */
	map(matrix, cb){
		return matrix.map((raw, rawIndex) => 
			raw.map((value, colIndex) => cb(value, [rawIndex, colIndex]))
		)
	},
	/**
	 * Ëé∑ÂèñËØ•ÂÖÉÁ¥†Âë®Âõ¥ÁöÑÂÄºÂíå‰ΩçÁΩÆ, È°∫Â∫è‰∏∫È°∫Êó∂ÈíàÊñπÂêë
	 *
	 * @param {[[]]} matrix
	 * @param {[]} position
	 */
	getAround(matrix, position){
		let width = matrix[0].length
		let height = matrix.length
		let [x, y] = position
		let all = []
		all.push([x - 1, y]) // ‰∏ä
		all.push([x, y + 1]) // Âè≥
		all.push([x + 1, y]) // ‰∏ã
		all.push([x, y - 1]) // Â∑¶
		all = all.filter(posi => posi[0] >= 0 && posi[0] < width && posi[1] >= 0 && posi[1] < height) // ËøáÊª§ËæπÁïåÂÖÉÁ¥†
		all = all.map(position => ({
			position,
			value: matrix[position[0]][position[1]]
		}))
		return all
	}
}