<template>
	<div class="control">
		<!-- ÁßªÂä®Á´ØÊéßÂà∂, ‰ΩøÁî®emojiÈîÆÁõò -->
		<div v-if="isMobile" class="mobile-control">
			<table class="keys">
				<tr>
					<td></td>
					<td @click="handleClick('up')">‚¨ÜÔ∏è</td>
					<td></td>
				</tr>
				<tr>
					<td @click="handleClick('left')">‚¨ÖÔ∏è</td>
					<td></td>
					<td @click="handleClick('right')">‚û°Ô∏è</td>
				</tr>
				<tr>
					<td></td>
					<td @click="handleClick('down')">‚¨áÔ∏è</td>
					<td></td>
				</tr>
			</table>
			<table class="keys">
				<tr>
					<td @click="handleClick('confirm')">‚úîÔ∏è</td>
					<td></td>
					<td @click="handleClick('reload')">üîÑ</td>
				</tr>
				<tr>
					<td></td>
					<td></td>
					<td></td>
				</tr>
				<tr>
					<td @click="handleClick('return')">‚Ü©Ô∏è</td>
					<td></td>
					<td @click="handleClick('cancel')">‚ùå</td>
				</tr>
			</table>
		</div>
		<!-- PCÁ´ØÊéßÂà∂, ‰ΩøÁî®ÈîÆÁõò -->
		<div class="pc-control" v-else>
			<p>W A S D ËøõË°åÁßªÂä®/ÈÄâÊã©</p>
			<p>Space Á°ÆËÆ§ÈÄâÊã©</p>
			<p>Esc ËøîÂõûËèúÂçï</p>
			<p>Z Êí§ÈîÄÊìç‰Ωú</p>
			<p>R ÈáçÊñ∞ËΩΩÂÖ•</p>
		</div>
	</div>
</template>
<script>
import Bus from '../utils/Bus'
import OPERATION from '../constant/Operation'

export default {
	name: 'GameControl',
	data(){
    return {
      isMobile: /Mobile/i.test(navigator.userAgent)
    }
  },
	mounted(){
		this.registerKey()
	},
	methods: {
		/**
		 * Â§ÑÁêÜ emoji ÊåâÈîÆ
		 */
		handleClick(key){
			Bus.$emit('control', OPERATION[key])
		},
		/**
		 * Ê≥®ÂÜåÈîÆÁõò‰∫ã‰ª∂
		 */
		registerKey(){
			let register = document.body.addEventListener
			let map = {
				'w': OPERATION.up,
				'a': OPERATION.left,
				's': OPERATION.down,
				'd': OPERATION.right
			}
			register('keyup', event => {
				let key = event.key
				if (Object.keys(map).includes(key)) { // ÊåâÈîÆ‰∏∫ w a s d Êó∂, ÁßªÂä®‰∫∫Áâ©
					Bus.$emit('control', map[key])
				}
				if (key === 'z') { // Êí§ÈîÄ‰∏äÊ¨°Êìç‰Ωú
					Bus.$emit('control', OPERATION.return)
				}
				if (key === 'r') { // ÈáçÂºÄ
					Bus.$emit('control', OPERATION.reload)
				}
				if (key === ' ') { // Á°ÆËÆ§
					Bus.$emit('control', OPERATION.confirm)
				}
				if (key === 'Escape') { // ÂèñÊ∂à
					Bus.$emit('control', OPERATION.cancel)
				}
			})
		}
	}
}
</script>
<style>
.mobile-control{
	font-size: 2em;
	margin-top: 40px;
}
.pc-control{
	margin-top: 40px;
}
.pc-control p{
	margin-top: 10px;
}
.keys{
	display: inline-block;
	margin: 0 20px;
}
.keys td{
	width: 44px;
	height: 44px;
}
</style>
